import {IndexModel} from '../../model/index.js'
const  indexModel = new IndexModel()
export default {
    name: "welfare",
    data(){
        return {
            list:[],
            page:1,
        }
    },
    methods:{
        getList()
        {
            let params = {
                page : this.page,
                size : 10
            }
            indexModel.getPackageList(params).then((res) => {
                this.list = res.data
            })
        }
    },
    onLoad(){
        this.getList()
    }
}
